<template lang="pug">
    #app.flex.flex-column.vh-100.avenir
        //- #top.bb.b--gray.bg-washed-yellow
        //-     h1 Community Map
        #middle.flex.flex-auto
            #sidebar.br.b--light-gray.overflow-auto.w3.w-25-ns.pa2
                h2 Victoria lockdown map
                p This unofficial map tries to show the area you can travel to under <a href="https://www.dhhs.vic.gov.au/updated-restrictions-announcement-2-august-covid-19">Victoria's Stage 4 restrictions</a>.
                p Click on the map or search for your address to set your home location.
                .stats
                    p(v-if="within === false")
                        | Your home is not within the Melbourne Metrpolitan boundary. Stage 3 restrictions apply, not Stage 4.
                    div(v-if="within === true")
                        p Your home is within the Melbourne Metrpolitan boundary. Stage 4 restrictions apply.
                        table.stats(v-if="this.area")
                            tr
                                th Accessible area
                                td {{ Number(area / 1e6).toLocaleString() }} km²
                        .button.bg-blue.link.ph2.pv2.dib.white(@click="clear") Clear
            #map-container.relative.flex-auto
                Map
                #overlay.absolute
                    //- #legend
        #bottom.bt.b--light-gray.flex-none
</template>

<script>
import Map from './components/Map.vue';
import FeatureInfo from './components/FeatureInfo.vue';
import { EventBus } from './EventBus';
// import flatten from 'array-flatten';
window.app = {};
import initTest from './initTest.json';
import flat from 'flat';
export default {
    name: 'app',
    components: {
        Map,
        FeatureInfo,
    },
    data() {
        return {
            area: undefined,
            within: undefined,
        };
    },
    created() {
        window.app.App = this;
        EventBus.$on('error', e => (this.error = e));
        EventBus.$on('homestats', stats => {
            this.area = stats.area;
            this.within = stats.within;
        });
    },
    computed: {},
    methods: {
        clear() {
            EventBus.$emit('clear-home');
        },
    },
};

require('tachyons/css/tachyons.min.css');
</script>

